import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
// 
import Login from '@/view/login'
/*(messageindex.vue)*/
import Index from '@/view/index/index'
import Message from '@/view/index/message'
import Readme from '@/view/index/readme'
/**/
import Order from '@/view/Order/index'
/**/
import VIP from '@/view/Vip/associator';
//
import worker from '@/view/Staff/worker'
import staff_add from '@/view/Staff/staff_add'
// 
import Goods from '@/view/Foods/goods'
// 
import Cost from '@/view/Cost/index'
// 
import Stock from '@/view/Stock/stock'
import OperatingStock from '@/view/Stock/operatingStock'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',redirect: '/login', 
      name: 'Hello',
      component: Hello
    }
    ,
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path:'/index',component:Index,
      meta: {
          breadcrumbName: ""
      },
      children:[
        {
          path:'message',
          component:Message,
          meta: {
            breadcrumbName: ""
          }
        },
        {
          path:'readme',
          component:Readme,
          meta: {
            breadcrumbName: ""
          }
        }
      ]
    },
    /**/
    {
      path:'/order',component:Index,
      meta: {
          breadcrumbName: ""
      },
      children:[
        {
          path:'index',
          component:Order,
          meta: {
            breadcrumbName: ""
          }
        }
      ]
    },

     /**/
    {
      path: '/vip', component: Index,
      meta: {
        breadcrumbName: ""
      },
      children:[{
        path: 'associator',
        component: VIP,
        meta: {
          breadcrumbName: ""
        }
      }]
    },

      //
    {
      path:'/renshi',component:Index,
      meta: {
            breadcrumbName: ""
      },
      children:[
        {path:'worker',component:worker,
          meta: {
            breadcrumbName: ""
          }
       },
        {path:'staff_add',component:staff_add,
          meta: {
            breadcrumbName: ""
          }
        }
      ]       
    },
    // 
    {
      path:'/foods',component:Index,
      meta: {
            breadcrumbName: ""
      },
      children:[{
        path: 'goods',component: Goods,
        meta: {
            breadcrumbName: ""
          }
      }]
    },
    
    // 
    {
      path:'/stock',component:Index,
      meta: {
          breadcrumbName: ""
      },
      children:[
        {
          path:'stock',
          component:Stock,
          meta: {
            breadcrumbName: ""
          }
        },
        {
          path:'operatingStock',
          component:OperatingStock,
          meta: {
            breadcrumbName: ""
          }
        }
      ]
    },

    // 
    {
      path:'/cost',component:Index,meta: {
            breadcrumbName: ""
          },
      children:[
        {
          path:'index',
          component:Cost,
          meta: {
            breadcrumbName: ""
          }
        }
      ]
    }



  ]
})



