<template>
<div class="v">
	<!--  -->
	<div>
		<div class="btn_plsc">
			<el-input placeholder=" /" style="width: 200px;" icon="edit" v-model="searchV">
			</el-input>
			<el-button type="primary" icon="search" @click="vipSearch"></el-button>
		</div>
		<el-button type="primary" class="page" icon="plus" @click="vipCreate"></el-button>
	</div>
	<!--  -->
	<div>
		<el-table :data="vipTableData.vipData" border stripe  style="width: 100%">
			<el-table-column header-align="center" width="52" type="selection">
			</el-table-column>
			<el-table-column header-align="center" width="56" type="index">
			</el-table-column>
			<el-table-column header-align="center" width="116" prop="vipID" label="" sortable>
			</el-table-column>
			<el-table-column header-align="center" width="116" prop="vipName" label="">
			</el-table-column>
			<el-table-column header-align="center" width="138" prop="vipPhone" label="" sortable>
			</el-table-column>
			<el-table-column header-align="center" width="138" prop="vipJoinDate" label="" sortable>
			</el-table-column>
			<el-table-column header-align="center" width="99" prop="vipScore" label="">
			</el-table-column>
			<el-table-column header-align="center" width="99" prop="vipGrade" label=""
			:filters="[{text:'',value:''},{text:'',value:''},{text:'',value:''}]"
			:filter-method="vipfilterTag" filter-placement="bottom-end">
			</el-table-column>
			<el-table-column header-align="center" width="122" prop="vipDiscount" label="" sortable>
			</el-table-column>
			<el-table-column header-align="center" label="">
				<template scope="scope">
					<el-button @click="vipEdit(scope.$index, scope.row)" icon="edit"></el-button>
					<el-button type="danger" 
					@click="viphandleDelete(scope.$index, scope.row)" icon="circle-cross"></el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>
	<!--  -->
	<div>
		<!--disabledtrue,false--> 
		<el-button type="warning" class="btn_plsc" icon="circle-cross"></el-button>
		<!--  -->
		<div class="page">
			<el-pagination @current-change="vipCurrentChange"
			 :page-size="8" :current-page.sync="vcurrentPage"
			layout="total, prev, pager, next, jumper" :total="vipTableData.vipData.length">
			</el-pagination>
			<!-- https://segmentfault.com/q/1010000010284670 -->
		</div>
	</div>
	<!--  -->
	<el-dialog :visible.sync="v_dialogFormVisible" title="" size="622px" @close="v_dialogClose">
		<el-form :model="addnewV" label-width="83px">
			<el-form-item label="">
				<el-input v-model="addnewV.vipID" style="width: 220px"></el-input>
				<el-alert title="" type="error" v-show="showEroll_v1" class="showE" @close="v_erollClose"></el-alert>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="addnewV.vipName" style="width: 220px"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="addnewV.vipPhone" style="width: 220px"></el-input>
				<el-alert title="" type="error" v-show="showEroll_v1" class="showE" @close="v_erollClose"></el-alert>
				<el-alert title="11" type="error" v-show="showEroll_v2" class="showE" @close="v_erollClose"></el-alert>
			</el-form-item>
			<el-form-item label="">
				<el-date-picker type="date" placeholder="" v-model="addnewV.vipJoinDate"  style="width: 220px">
				</el-date-picker>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="addnewV.vipScore" style="width: 220px"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="addnewV.vipGrade" style="width: 220px"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="addnewV.vipDiscount" style="width: 220px"></el-input>
			</el-form-item>
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click="v_dialogFormVisible = false"> </el-button>
			<el-button type="primary"  @click="v_CreateSubmit"> </el-button>
		</div>
	</el-dialog>
	<!--  -->
	<el-dialog :visible.sync="v_dialogEdit" title="" size="622px">
		<el-form :model="editV" label-width="83px">
			<el-form-item label="">
				<el-input v-model="editV.vipID" style="width: 220px"></el-input>
				<el-alert title="" type="error" v-show="showEroll_v1" class="showE" @close="v_erollClose"></el-alert>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="editV.vipName" style="width: 220px"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="editV.vipPhone" style="width: 220px"></el-input>
				<el-alert title="" type="error" v-show="showEroll_v1" class="showE" @close="v_erollClose"></el-alert>
				<el-alert title="11" type="error" v-show="showEroll_v2" class="showE" @close="v_erollClose"></el-alert>
			</el-form-item>
			<el-form-item label="">
				<el-date-picker type="date" placeholder="" v-model="editV.vipJoinDate"  style="width: 220px">
				</el-date-picker>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="editV.vipScore" style="width: 220px"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="editV.vipGrade" style="width: 220px"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="editV.vipDiscount" style="width: 220px"></el-input>
			</el-form-item>
		</el-form>
		<div slot="footer" class="v_dialogEditXG">
			<el-button @click="v_dialogEdit = false"> </el-button>
			<el-button type="primary" @click="v_EditSubmit"> </el-button>
		</div>
	</el-dialog>
	{{ OrderData }}
</div>
</template>

<script>
import store from '../../store/index';

export default {
    data() {
    	return {
    		vipTableData:{ vipData:[] },
			viplist:store.state.vip.vipData,
			vcurrentPage: 1, 
			searchV: "",
			v_dialogFormVisible: false,
			addnewV: {
				"vipID": "",
				"vipName": "",
				"vipPhone": "",
				"vipJoinDate": "",
				"vipScore": "330",
				"vipGrade": "",
				"vipDiscount": "9"
			},
			showEroll_v1: false,
			showEroll_v2: false,
			v_dialogEdit: false,
			editV: {
				"vipID": "",
				"vipName": "",
				"vipPhone": "",
				"vipJoinDate": "",
				"vipScore": "",
				"vipGrade": "",
				"vipDiscount": ""
			},
			editIndexV: "",
			OrderDataArray: []
    	}
    },
	// 
	mounted: function(){
		this.getData();
		this.getddData();
	},
	// 
	methods: {
		//
		getData: function(){
			let that = this;
			this.$http.get('../../static/dataJson/vipData.json').then(function(response){
				that.vipTableData = response.data
				that.vipTableData.vipData = that.viplist
			},function(response){
				console.log("")
			})
		},
		//  ,   ,
		getddData: function(){
			let that = this;
			this.$http.get('../../static/dataJson/OrderData.json').then(function(response){
				that.OrderDataArray = response.data.order
				for(let i=0 ; i < that.vipTableData.vipData.length ; i++){
					let iVP = that.vipTableData.vipData[i].vipPhone;
					for(let j=0 ; j < that.OrderDataArray.length ; j++){
						let jOP = that.OrderDataArray[j].buytel;
						if( iVP == jOP){
							console.log("",that.vipTableData.vipData[i].vipScore)
							that.vipTableData.vipData[i].vipScore = parseInt(that.vipTableData.vipData[i].vipScore)+10
							console.log("",that.vipTableData.vipData[i].vipScore)
						}
					}
					let vs = parseInt(that.vipTableData.vipData[i].vipScore);
					if( vs<660 ){
						that.vipTableData.vipData[i].vipGrade = "";
						that.vipTableData.vipData[i].vipDiscount = "9";
					}else if( vs<1200 ){
						that.vipTableData.vipData[i].vipGrade = "";
						that.vipTableData.vipData[i].vipDiscount = "8";
					}else{
						that.vipTableData.vipData[i].vipGrade = "";
						that.vipTableData.vipData[i].vipDiscount = "7";
					}
				}
			},function(response){
			})
		},
		// -
		vipfilterTag(value, row) {
			return row.vipGrade === value;
		},
		// -currentPage 
		vipCurrentChange(val) {
			console.log(`: ${val}`);
		},
		// 
		viphandleDelete(index, row) {
			// 
			this.vipTableData.vipData.splice(index,1)
		},
		//
	    vipCreate() {
	        this.v_dialogFormVisible = true;
	    },
	    //
	    v_CreateSubmit(){
	        let vm = this;
	        // 
	        let oldDate = vm.addnewV.vipJoinDate;
	        let year = new Date(oldDate).getFullYear();
	        let month = new Date(oldDate).getMonth()+1;
	        let day = new Date(oldDate).getDate();
	        if (month < 10) { month = "0" + month};
	        if (day < 10) { day = "0" + day};
	        vm.addnewV.vipJoinDate = year + "-" + month + "-" + day;

	        // 
	        let re = /^[0-9]+.?[0-9]*$/; //
	        let id = vm.addnewV.vipID;
	        let phone = vm.addnewV.vipPhone;
	        if ( !re.test(id) || !re.test(phone) ) {
	        	vm.showEroll_v1 = true;
	        }else if( phone.length != 11) {
	        	vm.showEroll_v2 = true;
	        }else{
	        	//console.log('' , vm.addnewV , vm.vipTableData.vipData)
		        vm.vipTableData.vipData.push(vm.addnewV)
		        //console.log('',vm.vipTableData.vipData)

				// 
		        this.v_dialogFormVisible = false;
	        }
	    },
	    //   
	    v_erollClose(){
	    	this.showEroll_v1 = false
			this.showEroll_v2 = false
	    },
	    //   
	    v_dialogClose(){
	    	this.addnewV = {
				"vipID": "",
				"vipName": "",
				"vipPhone": "",
				"vipJoinDate": "",
				"vipScore": "330",
				"vipGrade": "",
				"vipDiscount": "9"
			};
	    },
		// 
		vipEdit(index, row) {
			this.v_dialogEdit = true;
			// 
				// 
				/*this.editV.vipID = row.vipID;
				this.editV.vipName = row.vipName;
				this.editV.vipPhone = row.vipPhone;
				this.editV.vipJoinDate = row.vipJoinDate;
				this.editV.vipScore = row.vipScore;
				this.editV.vipGrade = row.vipGrade;
				this.editV.vipDiscount = row.vipDiscount;*/
				// 
				/*this.editV = row*/
				//   vue
				this.editV = JSON.parse(JSON.stringify(row))
			// 
			this.editIndexV = index;
		},
		//   
		v_EditSubmit(){
			// 
			let idx = this.editIndexV;
			// 
	        let oldDate = this.editV.vipJoinDate;
	        let year = new Date(oldDate).getFullYear();
	        let month = new Date(oldDate).getMonth()+1;
	        let day = new Date(oldDate).getDate();
	        if (month < 10) { month = "0" + month};
	        if (day < 10) { day = "0" + day};
	        this.editV.vipJoinDate = year + "-" + month + "-" + day;
			// 
			this.vipTableData.vipData.splice(idx,1,this.editV);
			this.v_dialogEdit = false;
			this.editV = {
				"vipID": "",
				"vipName": "",
				"vipPhone": "",
				"vipJoinDate": "",
				"vipScore": "",
				"vipGrade": "",
				"vipDiscount": ""
			}
		},
		// 
		vipSearch(){
			console.log(this.searchV)
		}
	}
}
</script>

<style>
	.v{
		padding: 25px;
	}
	.v .btn_plsc{
		float: left;
		margin: 12px 32px;
	}
	.v .page{
		float: right;
		margin: 12px 36px;
	}
	
	.showE{
		width: 228px;
		line-height: 12px;
	}
	.v_dialogEditXG{
		text-align: center;
	}
</style>


