'use strict';

const person = {
  state: {
    personInfo:{
      username:"admin",
      trueName:"",
      sex:"man",
      job:"",
      email:"Don123@qq.com",
      introduction:',!:'
    }
  },

  mutations: {
      //statevuexuserstate
      //newUserInfoSET_USERINFO
      SET_PERSONINFO: (state, newPersonInfo) => {
        state.personInfo = newPersonInfo;
        console.log(':',state.personInfo)
      },
    },
    //Action  store.dispatch 
    actions: {
      //newUserInfo
      setPersonInfo({ dispatch,commit, state},newPersonInfo){
        console.log('',newPersonInfo)
        commit('SET_PERSONINFO',newPersonInfo)
      }
    } 
};

export default person;


