<template>
<div>
	<!-- ============================================================================================ -->
<el-collapse >
  <!-- template -->
  <el-collapse-item title="" name="5">
    <el-table :data="form0.test">  
            <el-table-column align="center" label='' width="100" > <!-- prop="thing" -->
            <!-- proptempla================================== -->
              <template scope="test"> 
                <span>{{test.row.thing}}<i class="fs iconfont icon-accessory"></i></span>
              </template> 
            <!-- ============================ -->
            </el-table-column>        
            <el-table-column align="center" label="" width="" >
            <!-- template============================== -->
                <!-- <template scope="scope">
                  <el-input v-show="scope.row.edit" size="small" v-model="scope.row.money" ></el-input>
                  <span v-show="!scope.row.edit" >{{ scope.row.money }}</span>
                </template> -->
            <!-- ============================== -->
        <!-- v-if==================================================================== -->
            <template scope="test">
                  <span v-if="editIndex !== test.$index">{{ test.row.money }}</span>
                  <el-input size="small" v-model="inputTest.money" v-else></el-input>
            </template>  
        <!-- =============================================================================== -->
            </el-table-column>
            <el-table-column align="center"  label="" >
                <!-- ====== -->
                  <template scope="test">
                  <!-- <el-button v-show='!scope.row.edit' type="primary" v-on:click="scope.row.edit=true" size="small" icon="edit"></el-button>
                  <el-button v-show='scope.row.edit' type="success" @click="scope.row.edit=false" size="small" icon="check"></el-button> -->
                <!-- ============================== -->
        <!-- v-if==================================================================== -->
            <el-button  type="primary" @click="editTest(test.$index,test.row)" size="small" icon="edit" v-if="editIndex !== test.$index"></el-button>
            <el-button type="success" @click="finishTest(test.$index)" size="small" icon="check" v-else></el-button>
         <!-- =============================================================================== -->
                     <el-button icon="delete" size="small" type="danger" @click="Delete(test.$index, test.row)"></el-button>
                  </template>
            </el-table-column>
    </el-table> 
        <!-- ==================================================================== -->
          <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form5" class="small-space" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
              <el-form-item label="">
                <el-input v-model="form5.thing"></el-input><!-- v-model="roleTemp.roleName" -->
              </el-form-item>       
        <el-form-item label="">
                <el-input v-model="form5.money"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false"> </el-button><!--  -->            
              <el-button type="primary" @click="Submit"> </el-button>
            </div>
      </el-dialog>
       <!--  ====================================================================== -->
    <el-button class="filter-item" type="primary" @click="dialogFormVisible = true"  icon="edit" ></el-button>
      <p>{{ totaltest }}</p>
  </el-collapse-item>
			<!-- ============================================================================================ -->
</el-collapse>
</div>
</template>
<script>
	import store from '../../store/index'
	  export default {
	    name: 'index',
	    data() {
	      return {
	        //json
	      	form0:{ 
	            test:[]  
			},
	        storecostlist:store.state.cost.costlist,
	        //       	
	        activeNames: ['1'],
	        //
	        //
	        dialogFormVisible: false,
	        form5: {
	          thing: '',
	          money: ''
	        },
	        //
	        editIndex:'',
	        //input
	        inputTest:{
	          money:""
	        }
	      };
	    },
	    mounted:function(){
	    	this.getData();
	        this.add;
	    },
	    computed:{
	        totaltest:function(){
	            let test=0;
	            for(let i=0;i<this.form0.test.length;i++){
	                let t=this.form0.test[i].money;
	                let t2=parseInt(t)
	                test+=t2;
	            } 
	            return test
	        },
	    },
	    methods:{
	        //
	        Submit(){
	            var that = this;
	            console.log('',that.form5)
	            that.form0.test.push(that.form5)
	            console.log('',that.form5)             
	            that.dialogFormVisible = false;
	            that.form5={
	              thing: '',
	              money: ''
	            }
	        },
	        //
	        Delete(index,row){
	            var that = this;
	            console.log('row',index,'-----',row);
	            that.form0.test.splice(index,1)
	        },
	        //
	        editTest(index,row){
	          this.editIndex=index;
	          this.inputTest=JSON.parse(JSON.stringify(row))
	          console.log(this.inputTest)
	        },
	        // 
	        finishTest(index){
	          this.editIndex=null
	          this.form0.test[index].money=this.inputTest.money
	          console.log(this.form0.test[index].money)
	        },
	        //json
	        getData:function(){
	        	let that = this;
	        	that.$http.get('../../static/dataJson/Cost.json').then(
	        	function(response){
	        		console.log(response);
	                // alert("")
	        		console.log("json",response.data)
	        		that.form0 = response.data;
	            	this.form0 =this.storecostlist
	        	},function(response){
	        		alert("")
	        	})
	        },    
	  }
	  }
</script>
<style>
    .el-input--small .el-input__inner{
        width:100px;
        text-align: center;
        font-size: 14px;
    }
</style>


