<template>
	<div>
	  <div class="rs_btn">
	  <div class="rs_inp"><el-input v-model="input" placeholder=""></el-input></div>
	  	 <!--  -->
        <el-button class="rs_query"></el-button>
		<el-button></el-button>
	  </div>
	  
		<el-table
		    :data="tableData3.worker"  
		    border
		    highlight-current-row
		    style="width: 100%">
		    <el-table-column type="selection" header-align="center" width="60" > </el-table-column>
		    <!-- <el-table-column prop="rs_number" label="" width="100"></el-table-column> -->
		    <el-table-column type="index"header-align="center" label="" width="70"></el-table-column>  <!--  -->
		    <el-table-column label="" header-align="center" >
		    	<template scope="scope">{{scope.row.rs_name}}</template>
		    </el-table-column>
		    <el-table-column prop="rs_sex" label="" header-align="center">
		    	<template scope="scope">{{scope.row.rs_sex}}</template>
		    </el-table-column>
		    <el-table-column prop="rs_id" label="" header-align="center">
		    	<template scope="scope">{{scope.row.rs_id}}</template>
		    </el-table-column>
		    <el-table-column prop="rs_post" label="" header-align="center">
		    	<template scope="scope">{{scope.row.rs_post}}</template>
		    </el-table-column>
		    <el-table-column prop="rs_limit" label="" header-align="center">
		    	<template scope="scope">{{scope.row.rs_limit}}</template>
		    </el-table-column>
		    <el-table-column label="" header-align="center">
		    	<template scope="scope">{{scope.row.rs_price}}</template>
		    </el-table-column>
		    <el-table-column prop="rs_desc" header-align="center" label="">
		    	<template scope="scope">{{scope.row.rs_desc}}</template>
		    </el-table-column>
		    <el-table-column label="" header-align="center" width="160" >
	        <template scope="scope">
          	<!-- <router-link to="/staff_edit"></router-link> -->
            <el-button  size="small" @click="handleEdit(scope.$index, scope.row)" class="rs_query"></el-button> 
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
	       </el-table-column>
		</el-table>

			<!--  -->
		 <el-dialog title="" :visible.sync="dialogFormVisible" size="420px">
		   <el-form v-model="form" class="rs_dialogbody">
		      <el-form-item label="" :label-width="formLabelWidth">
		         <el-input v-model="form.rs_name" auto-complete="off"></el-input>
		      </el-form-item>
			  <el-form-item label="" :label-width="formLabelWidth"> 
			     <el-radio-group v-model="form.rs_sex">
			        <el-radio label=""></el-radio>
			        <el-radio label=""></el-radio>
			     </el-radio-group>
			  </el-form-item>
			  <el-form-item label="" :label-width="formLabelWidth">
				  <el-input v-model="form.rs_id"></el-input>
			  </el-form-item>
		      <el-form-item label="" :label-width="formLabelWidth">
		        <el-select v-model="form.rs_post" placeholder="">
		           <el-option label="" value=""></el-option>
	         	        <el-option label="" value=""></el-option>
	         	        <el-option label="" value=""></el-option>
	         	        <el-option label="" value=""></el-option>
	         	        <el-option label="" value=""></el-option>
		        </el-select>
		      </el-form-item>
		       <el-form-item label="" :label-width="formLabelWidth">
		        <el-select v-model="form.rs_limit" placeholder="" >
		          <el-option label="" value=""></el-option>
		          <el-option label="" value=""></el-option>
		          <el-option label="" value=""></el-option>
		        </el-select>
		      </el-form-item>
		       <el-form-item label="" :label-width="formLabelWidth">
		          <el-input v-model="form.rs_price" auto-complete="off"></el-input>
		      </el-form-item>
		      <el-form-item label="" :label-width="formLabelWidth">
		        <el-input type="textarea" v-model="form.rs_desc"></el-input>
		      </el-form-item>
		    </el-form>
		    
		   <div slot="footer" class="rs_dialogfooter">
		     <el-button @click="dialogFormVisible = false"> </el-button>
		     <el-button type="primary" @click="ediSure"> </el-button>
		   </div>
		 </el-dialog>
		
		  <!-- <p>{{s}}</p> -->
	</div>
</template>

<script>
  import store from '../../store/index';
	export default {
	    data() {
	      return {
	        tableData3:{
	        	worker:[]  //json
	        },
	        input: '',//
	        s:store.state.staff.staffManagment,
	        dialogFormVisible: false,
	        form: {
	                 rs_name: "", 
         		     rs_sex: "",
         		     rs_id:"", 
         		     rs_post:"",
         		     rs_price:"",
         		     rs_limit:"",
         		     rs_desc:""
	               },
	         formLabelWidth: '50px',
	         index0:null,
	         afterEdit:store.state.staffCost.staffCostState
	        }

	   },
	    mounted:function(){   //
	   		this.getData();
	   		console.log(""+store.state.staff.staffManagment)
	   },
	    methods: {
         //
         handleEdit(index, row){
         	this.dialogFormVisible = true;
         	//JSON.parse(JSON.stringify(row))  
         	//data
         	this.form=JSON.parse(JSON.stringify(row));  //
         	console.log(this.form);
         	this.index0=index;
         },
         ediSure(){
         	this.tableData3.worker.splice(this.index0,1,this.form);
         	console.log(">>>>>"+this.form);
         	console.log(this.tableData3.worker);
         	store.dispatch('commitCost',this.tableData3.worker);//
         	this.dialogFormVisible = false;
         },
         handleDelete(index, row) {  //
         	var rs_sure=this;
           //
            this.$confirm(', ?', '', {  //
                 confirmButtonText: '',
                 cancelButtonText: '',
                 type: 'warning'
                 }).then(() => {
                 type: 'success'
              	 rs_sure.tableData3.worker.splice(index,1);
              	 store.dispatch('commitCost',this.tableData3.worker);
                 }).catch(() => {
                 this.$message({
                 type: 'info',
                 message: ''
                 });          
                 });
         },
         
         getData:function(){  //json
            let staff=this;
         	this.$http.get("../../static/dataJson/workerData.json").then(function(response){
            //
         	 staff.tableData3=response.data;//jsontableData3
         	 var arr=staff.s;  //arr
         	 console.log("arr>>>>>>>>",arr)
         	 for(let i=0;i<arr.length;i++){  //
         	  	staff.tableData3.worker.push(arr[i])
         	 }
         	 staff.tableData3.worker=this.afterEdit;//
         	 },function(response){
         	  alter(" T_T ")
         	});
          }
       },
         computed:{
       	  
       }
	  }
</script>
<style>
	
	    .rs_btn{width:400px; margin-bottom: 10px;}
	    .rs_inp{width:200px;float:left;margin-right:5px;}
	    .rs_dialog{width:400px;}
	    .rs_dialogbody{text-align:left;margin-bottom:-30px}
	    .rs_dialogfooter{text-align: center}

	   
</style>



