<template>
	<div class="rs_bor">
	<p></p>
	<el-form ref="form" :model="form" label-width="50px" style="text-align:left">
	  <el-form-item label="">
	    <el-input v-model="form.rs_name"></el-input>
	  </el-form-item>
	   <el-form-item label="">
	    <el-radio-group v-model="form.rs_sex">
	      <el-radio label=""></el-radio>
	      <el-radio label=""></el-radio>
	    </el-radio-group>
	  </el-form-item>
	  <el-form-item label="">
	    <el-input v-model="form.rs_id"></el-input>
	  </el-form-item>
	  <el-form-item label="">
	    <el-select v-model="form.rs_post" placeholder="">
	      <el-option label="" value=""></el-option>
	      <el-option label="" value=""></el-option>
	      <el-option label="" value=""></el-option>
	      <el-option label="" value=""></el-option>
	      <el-option label="" value=""></el-option>
	    </el-select>
	  </el-form-item>
	   <el-form-item label="">
	    <el-select v-model="form.rs_limit" placeholder="">
	      <el-option label="" value=""></el-option>
	      <el-option label="" value=""></el-option>
	      <el-option label="" value=""></el-option>
	    </el-select>
	  </el-form-item>
	  <el-form-item label="">
	    <el-input type="textarea" v-model="form.rs_desc"></el-input>
	  </el-form-item>

	  <el-form-item class="rs_addfooter">
	    <el-button type="primary" @click="onSubmit"></el-button>
	    <el-button></el-button>
	  </el-form-item>
	</el-form>
	</div>
</template>
<script>
  import store from '../../store/index';
     export default {
	    data() {
	      return {
	      	add:[],
	        form: { 
		     rs_name: "", 
		     rs_sex: "",
		     rs_id:"", 
		     rs_post:"",
		     rs_limit:"",
		     rs_desc:""
	        }
	      }
	    },
	    methods: {
	      onSubmit() {
	      	var s=JSON.parse(JSON.stringify(this.form));
	        this.add.push(s);
	        store.dispatch('setstaff',this.add);//action
	        store.dispatch('commitCost',this.add);
	        this.$router.push({path:'/renshi/worker'})
	      }
	    }
	 }
</script>

<style>
	.rs_bor{border: 1px solid #002e5b;
	        border-radius: 8px;
	        width:400px;
	        padding:20px;
	        margin: auto;
	    }
	.rs_addfooter{text-align: center}
   p{
   	font-size: 20px;
   	font-weight:bold;
   	margin-bottom:20px;
   }
</style>


