<template>
  <div>
  <el-collapse>  
  <el-collapse-item title="" name="4">
    <el-table :data="form0.other" >
            <el-table-column align="center" label='' width="300">
              <template scope="scope">
                  <span v-if="editIndex !== scope.$index">{{ scope.row.thing }}</span>
                  <el-input  size="small" v-model="scope.row.thing" v-else></el-input>
              </template> 
            </el-table-column>        
            <el-table-column align="center" label="" width="300" >
              <template scope="scope">
                  <span v-if="editIndex !== scope.$index">{{ scope.row.money }}</span>
                  <el-input  size="small" v-model="inputOther.money" v-else></el-input>
              </template>  
            </el-table-column>
      <el-table-column align="center"  label="" >
                  <template scope="scope">
                      <el-button  type="primary" @click="editOther(scope.$index,scope.row)" size="small" icon="edit" v-if="editIndex !== scope.$index"></el-button>
                      <el-button type="success" @click="finishOther(scope.$index)" size="small" icon="check" v-else></el-button>
                     <el-button icon="delete" size="small" type="danger" @click="Delete(scope.$index, scope.row)"></el-button>
                  </template>
        </el-table-column>
      </el-table> 
        
            <!--  ====================================================================== -->
          <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form" class="small-space" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
              <el-form-item label="">
                <el-input v-model="form.thing"></el-input><!-- v-model="roleTemp.roleName" -->
              </el-form-item>       
        <el-form-item label="">
                <el-input v-model="form.money"></el-input>
              </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false"> </el-button><!--  -->            
              <el-button type="primary" @click="Submit"> </el-button>
            </div>
      </el-dialog>
    <el-button class="filter-item" type="primary" @click="dialogFormVisible = true"  icon="edit" ></el-button>
      <p>{{ totalother }}</p>
  </el-collapse-item>

  </el-collapse>
  </div>
</template>

<script>
import store from '../../store/index'
  export default {
    name: 'index',
    data() {
      return {
        //json
        form0:{           
            other:[],
       },
      storecostlist:store.state.cost.costlist,
        //        
        activeNames: ['1'],
        //
        dialogFormVisible: false,
        //
        form: {
          thing: '',
          money: ''
        },
        //
        edit:{},
        //
        editIndex:'',
        //input
        inputOther:{
          thing:"",
          money:""
        }
      };
    }, 
    mounted:function(){
      this.getData();
      this.add; 
    },
    computed:{ 
        //
        totalother:function(){
            let other=0;
            for(let i=0;i<this.form0.other.length;i++){
                let o=this.form0.other[i].money;
                let o2=parseInt(o)
                other+=o2;
            } 
            return other
        }, 
    },
    methods:{
        //
        Submit(){
            var that = this;
            console.log('',that.form)
            that.form0.other.push(that.form)
            console.log('',that.form)             
            that.dialogFormVisible = false;
            that.form={
              thing: '',
                money: ''
            }
        },
        Delete(index,row){
            var that = this;
            console.log('row',index,'-----',row);
            that.form0.other.splice(index,1)
        },
        //
        editOther(index,row){
          this.editIndex=index;
          this.inputOther=JSON.parse(JSON.stringify(row))          
        },
        // 
          finishOther(index){
          this.editIndex=null
          this.form0.other[index].thing=this.inputOther.thing
          this.form0.other[index].money=this.inputOther.money          
          },
        //json
        getData:function(){
          let that = this;
          that.$http.get('../../static/dataJson/Cost.json').then(
          function(response){
            console.log(response);
                // alert("")
            console.log("json",response.data)
            that.form0 = response.data;
            this.form0 =this.storecostlist
          },function(response){
            alert("")
          })
        },    
  }
  }
</script>
<style>
    .el-input--small .el-input__inner{
        width:100px;
        text-align: center;
          font-size: 14px;
    }
</style>


