<template>
<div>
  <el-table :data="tableData" class="tb-edit" style="width: 100%" highlight-current-row @row-click="handleCurrentChange">
    <el-table-column label="" width="180">
        <template scope="scope">
            <el-input size="small" v-model="scope.row.date" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input> <span>{{scope.row.date}}</span>
        </template>
    </el-table-column>

    <el-table-column label="" width="180">
        <template scope="scope">
            <el-input size="small" v-model="scope.row.name" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input> <span>{{scope.row.name}}</span>
        </template>
    </el-table-column>

    <el-table-column prop="address" label="">
        <template scope="scope">
            <el-input size="small" v-model="scope.row.address" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input> <span>{{scope.row.address}}</span>
        </template>
    </el-table-column>

    <el-table-column label="">
        <template scope="scope">
        <!--<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>-->
        <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
    </el-table-column>

   </el-table>
   <br>:{{tableData}}
</div>
</template>

<script>
export default{

data: {
    tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 '
        },{
        date: '2016-05-04',
        name: '',
        address: ' 1517 '
        }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519 '
        }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516 '
        }]
},
methods: {
    handleCurrentChange(row, event, column) {
        console.log(row, event, column, event.currentTarget)
    },
    handleEdit(index, row) {
        console.log(index, row);
    },
    handleDelete(index, row) {
        console.log(index, row);
    }
}
 
}

</script>

<style>
.tb-edit .el-input {
    display: none
}
.tb-edit .current-row .el-input {
    display: block
}
.tb-edit .current-row .el-input+span {
    display: none
}


</style>


